<template>
  <div>
    <img class="room-img" :src="oneroom.image" alt="" @click="onEmit()" />
    <h4 class="room-tit" @click="onEmit()">{{ oneroom.title }}</h4>
    <p>{{ oneroom.price }} 만원</p>
    <button class="btn" @click="onEmitCout()">허위매물신고</button>
    <span class="report-txt"
      >신고수 : <strong>{{ oneroom.count }}</strong></span
    >
  </div>
</template>

<script>
export default {
  name: "Card",
  data() {
    return {
      value: false,
    };
  },
  props: {
    oneroom: Object,
  },
  methods: {
    onEmit() {
      this.$emit("modalOpen", this.oneroom.id);
    },
    onEmitCout() {
      this.$emit("countUp", this.oneroom.id);
    },
  },
  components: {},
};
</script>

<style>
.report-txt {
  margin-left: 12px;
}
.room-tit {
  cursor: pointer;
}
</style>
