<template>
  <div class="discount">
    지금 결제하면 <strong>{{ discountNum }}%</strong> 할인
    <span
      ><strong>{{ result }}</strong></span
    >
  </div>
</template>

<script>
export default {
  name: "Discount",
  data() {
    return {
      discountNum: 30,
      discountDay: localStorage.getItem("discountDay"),
      result: "",
    };
  },

  methods: {
    discoutingDay() {
      const seconds = Math.floor(this.discountDay / 1000);
      const minutes = Math.floor(seconds / 60);
      const hours = Math.floor(minutes / 60);
      const days = Math.floor(hours / 24);

      const remainingSeconds = seconds % 60;
      const remainingMinutes = minutes % 60;
      const remainingHours = hours % 24;

      return `${days}일 ${remainingHours}시간 ${remainingMinutes}분 ${remainingSeconds}초 남음`;
    },
  },
  mounted() {
    localStorage.setItem("discountDay", 854534232334);
    setInterval(() => {
      if (this.discountDay >= 1) {
        this.discountDay -= 1000;
        localStorage.setItem("discountDay", this.discountDay.toString());
        this.result = this.discoutingDay(this.discountDay);
      } else {
        clearInterval();
      }
    }, 1000);
  },
};
</script>

<style>
.discount {
  background: #eee;
  padding: 30px;
  font-size: 24px;
  margin: 10px;
  border-radius: 5px;
}
</style>
