<template>
  <div class="black-bg">
    <div class="white-bg">
      <Discount />
      <img class="room-img" :src="oneroom[target].image" alt="" />
      <h4>{{ oneroom[target].title }}</h4>
      <p>{{ oneroom[target].content }}</p>
      <span class="month-txt">개월수 선택</span>
      <select name="" id="" v-model="month">
        <option value="1">1</option>
        <option value="6">6</option>
        <option value="12">12</option>
        <option value="24">24</option>
      </select>
      <p>
        {{ month }} 개월의 가격은

        <strong>{{
          Number(oneroom[target].price * month).toLocaleString()
        }}</strong>
        입니다.
      </p>
      <button class="btn" @click="onEmit">닫기</button>
    </div>
  </div>
</template>

<script>
import Discount from "./Discount.vue";
export default {
  name: "Modal",
  data() {
    return {
      month: 1,
      value: false,
    };
  },
  props: {
    oneroom: Array,
    target: Number,
    modalControl: Boolean,
  },
  methods: {
    onEmit() {
      this.$emit("modalClose");
    },
  },
  components: {
    Discount,
  },
  watch: {
    month(e) {
      if (isNaN(e)) {
        alert("숫자만 입력가능 합니다.");
        this.month = 1;
      }
    },
  },
};
</script>

<style>
.black-bg {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  padding: 20px;
}
.white-bg {
  width: 100%;
  max-width: 780px;
  margin: 0 auto;
  background: #fff;
  border-radius: 8px;
  padding: 20px;
}
.month-txt {
  font-size: 14px;
  margin-right: 12px;
}
</style>
